@import '../../common/styles/sass/variables';

.mainBlock {
  width: 100%;
  display: flex;
  align-items: center;
  min-height: 100vh;
  flex-direction: row;
  justify-content: center;
  background-color: $bgColor;

  .container {
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-top: 30px;

    .text {
      display: flex;
      flex-direction: column;
      font-size: 20px;
      padding: 30px;
      color: $textColor;
      font-family: $titleFontFamily;
      @media screen and (max-width: 945px) {
        width: max-content;
      }

      h1 {
        margin: 0;
        font-weight: 500;
        font-size: 40px;
        line-height: 1.4074;
        word-break: break-word;
        color: $primaryColor;
        @media screen and (max-width: 945px) {
          font-size: 20px;
          line-height: 49px;
        }
      }

      p {
        margin: 0;
        color: $textColor;
        font-size: 20px;
      }

    }

    .tilt {

      width: 50vh;
      height: 70vh;
      object-fit: cover;
      z-index: 1;
      @media screen and (max-width: 945px) {
        width: 100%;
      }

      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
        border-radius: 10px;
      }

      &:hover {
        transform: rotateY(10deg) rotateX(-5deg) scale(1.05);
      }
    }


  }
}




